import {
    Box,
    Button,
    Card,
    // Container,
    Grid,
    // Link,
    TextField,
    Divider,
    CardContent,
    CardHeader,
    Typography
} from '@material-ui/core';
import React from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import BoardService from '../../service/BoardService';

const ReadBoardComponent = () => {
    const history = useNavigate();
    const { state } = useLocation();
    // const { pIdx, pBoard } = state;
    const pIdx = state.idx;
    const pBoard = state.board;

    // # 2.   api    
    // useEffect(() => {
    //     BoardService.getOneBoard(idx).then(res => {
    //         setIdx(res.data.getIdx);
    //         setBoard(res.data);
    //         console.log(idx);
    //         console.log(board);
    //         console.log(res);
    //     });
    // }, []);

    // # 3.        /     
    const returnBoardType = (categoryNo) => {
        let category = null;
        if (categoryNo === 0) {
            category = "";

        } else if (categoryNo === 1) {
            category = "";

        } else if (categoryNo === 2) {
            category = "";

        } else {
            category = "";
        }

        return (
            <Typography
                variant="h4"
                gutterBottom>
                 : {category}
            </Typography>
        )
    }

    const returnDate = (cTime) => {
        console.log(" : ", { cTime });
        // https://developer.mozilla.org/ko/docs/Web/JavaScript/Reference/Global_Objects/Intl/DateTimeFormat (date format )
        console.log(new Intl.DateTimeFormat('ko-KR').format(cTime));
        return (
            <div className="row">
                <h3> : [ {new Intl.DateTimeFormat('ko-KR').format(cTime)} ] </h3>
            </div>
        );
    }

    // # 4.      
    const goToList = () => {
        history('/app/board/1', { replace: true });
    }

    const goToUpdate = (event) => {
        event.preventDefault();
        // this.props.history.push(`/create-board/${this.state.index}`);
        history(`/app/create-board/${pIdx}`, {
            replace: true,
            state: {
                idx: pIdx,
                board: pBoard
            }
        });
    }

    const deleteView = async function () {
        if (window.confirm("  ?")) {
            // BoardService.deleteBoard(this.state.index).then( res => {
            BoardService.deleteBoard(pIdx).then(res => {
                console.log("delete result => ", JSON.stringify(res));
                if (res.status === 200) {
                    history('/app/board/1');
                } else {
                    alert("  .");
                }
            })
        }
    }

    const textFieldStyle = {
        marginTop: 8,
        width: '80%',
        height: "550px  "
    }



    return (
        <Box>
            <Card>
                <CardHeader
                    variant="h2"
                    subheader=" "
                    title=""
                />
                <Divider />
                <CardContent>
                    <Grid >
                        <Typography
                            variant="h3"
                            marginBottom="25px"
                        >
                             : {pBoard.title}
                        </Typography>
                    </Grid>
                    <Grid
                        marginBottom="50px">
                        <Typography
                            marginBottom="25px"
                            marginRight="350px"
                            align="right">
                            {returnBoardType(pBoard.category)}
                        </Typography>
                        <TextField
                            defaultValue={pBoard.content}
                            label=""
                            InputProps={{
                                readOnly: true,
                            }}
                            size="medium"
                            margin="normal"
                            multiline="true"
                            rows="20"
                            rowsMax="20"
                            style={textFieldStyle}
                        />
                    </Grid>
                    <Grid
                        marginRight="300px"
                        marginBottom="50px">
                        <Typography
                            align="right">
                            {returnDate(pBoard.createdTime, pBoard.updatedTime)}
                        </Typography>

                    </Grid>
                    <Grid
                        marginRight="300px"
                        align="right">
                        <Button
                            onClick={goToList}
                            style={{ marginLeft: "10px" }}
                            variant="contained"
                            color="primary"
                        >
                              
                        </Button>
                        <Button
                            onClick={goToUpdate}
                            style={{ marginLeft: "10px" }}
                            variant="contained"
                            color="primary"
                        >
                             
                        </Button>
                        <Button
                            onClick={() => deleteView()}
                            style={{ marginLeft: "10px" }}
                            variant="contained"
                            color="primary"
                        >
                             
                        </Button>
                    </Grid>
                </CardContent>
            </Card >
        </Box>
    );
}

export default ReadBoardComponent;